import React, {Fragment, useState, useContext} from 'react'
import { RateContext } from '../../context/RateContext'
import { Login } from '../login/Login'
import { Register } from '../register/Register'
import './modal.scss'

export const Modal =()=> {

    const {state, modalHideHandler}=useContext(RateContext)
    const [value, setValue]=useState('login')
    const links = [{name:'Input',id:'login'},{name:'Registration',id:'register'}]
    const cls=['modal']

    const windowHandler=(id)=>{
        setValue(id)
    }

    if(state.showModal){
        cls.push('modalShow')
    }

    return(
        <div className={cls.join(' ')}>
            <Fragment>
            <div className='modalHead'>
                <ul>
                    {links.map((item,i)=>{
                        return(
                            <li key={item.name} 
                                style={{fontWeight:item.id===value?'bold':'normal', cursor:'pointer'}}
                                onClick={()=>windowHandler(item.id)}>{item.name}</li>
                        )
                    })}
                    {/* <li>Input</li>
                    <li>Registration</li> */}
                </ul>
                <i className='fa fa-times' aria-hidden='true' onClick={modalHideHandler}></i>
            </div>
            <hr></hr>
            </Fragment>

            <h2 style={{color:'red', width:'fit-content', margin:'0 auto'}}>{state.error}</h2>
            {value==='register'?<Register></Register> :<Login></Login>}
            {/* <Register></Register> */}
        </div>
    )
}